<template>
    <el-dialog
        title="更新模板"
        :visible.sync="visible"
        width="30%"
        center>
        <el-upload
            class="upload-demo"
            ref="upload"
            :action="url"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :before-upload="beforeUploadHandle"
            :file-list="fileList"
            :auto-upload="false">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <div slot="tip" class="el-upload__tip">注意：模板更新 <a href="/static/excel/18_data.xlsx">测试测试</a> </div>
        </el-upload>
        <span slot="footer" class="dialog-footer">
            <el-button @click="visible = false">取 消</el-button>
            <el-button type="primary" @click=" submitUpload()">确 定</el-button>
        </span>
    </el-dialog>
</template>

<script>
    export default {
      data () {
        return {
          visible: false,
          fileList: [],
          url: ''
        }
      },
      methods: {
        init (id) {
          this.visible = true
          this.url = this.$http.adornUrl(`/codetemplate/import?token=${this.$cookie.get('token')}&id=` + id)
          if (this.$refs.upload) {
            this.$refs.upload.clearFiles()
          }
        },
        submitUpload () {
          this.$refs.upload.submit()
          this.$message({
            message: '操作成功',
            type: 'success',
            duration: 1500,
            onClose: () => {
              this.visible = false
              this.$emit('refreshDataList')
            }
          })
        },
        handleRemove (file, fileList) {
            // console.log(file, fileList)
        },
        handlePreview (file) {

        },
        beforeUploadHandle (file) {
          if (!file.name.endsWith('.btl') && !file.name.endsWith('.html')) {
            this.$message.error('模板文件格式！')
            return false
          }
        }
      }
    }
</script>
<style scoped>

</style>



