<template>
  <div class="mod-config">
    <div ref="codetemplateFrom" style="margin-bottom: 20px;">
      <el-card class="filter-container" shadow="never">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button style="float:right" type="primary" @click="getDataList()" size="small">
            <icon-svg name="chaxun" class="icon"></icon-svg>查询搜索
          </el-button>
          <el-button v-if="isAuth('codetemplate:add')" type="primary"  size="small" style="float:right;margin-right: 15px" @click="addOrUpdateHandle()"><icon-svg name="addNew" class="icon"></icon-svg>  新增</el-button>
          <el-button v-if="isAuth('codetemplate:delete')" type="danger"  size="small" style="float:right" @click="deleteHandle()" :disabled="dataListSelections.length <= 0"><icon-svg name="shanchu1" class="icon"></icon-svg> 批量删除</el-button>
          <el-button  style="float:right;" @click="handleResetSearch()" size="small">重置</el-button>
        </div>
        <div style="margin-top: 15px;">
          <el-form :inline="true" :model="dataForm"  @keyup.enter.native="getDataList()">
            <el-row>
              <el-col :span="6">
              <el-form-item label="模板名称：">
                <el-input v-model="dataForm.templateName" placeholder="模板名称" clearable></el-input>
              </el-form-item>
            </el-col>
            <!--  <el-col :span="6">
                <el-form-item label="模板名称：">
                  <el-input v-model="dataForm.templateName" placeholder="模板名称" clearable></el-input>
                </el-form-item>
              </el-col>-->
             <!-- <el-col :span="6">
                <el-form-item label="模板名称：">
                  <el-input v-model="dataForm.templateName" placeholder="模板名称" clearable></el-input>
                </el-form-item>
              </el-col>-->
              <el-col :span="6">
            <el-form-item label="模板组：">
              <!--<el-input v-model="dataForm.templateName" placeholder="模板组" clearable></el-input>-->
              <el-select v-model="dataForm.groupId" placeholder="模板组">
                <el-option
                  v-for="item in options"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>

                <template slot="prefix">模板名称</template>
              </el-select>
            </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="6">
                <el-form-item >
                  <el-input v-model="dataForm.templateName" placeholder="模板名称" clearable>
                    <template slot="prepend">模板名称</template>
                  </el-input>
                </el-form-item>
              </el-col>
                <el-col :span="6">
                  <el-form-item label="模板名称：">
                    <el-input v-model="dataForm.templateName" placeholder="模板名称" clearable></el-input>
                  </el-form-item>
                </el-col>
               <el-col :span="6">
                 <el-form-item label="模板名称：">
                   <el-input v-model="dataForm.templateName" placeholder="模板名称" clearable></el-input>
                 </el-form-item>
               </el-col>
              <el-col :span="6">
                <el-form-item >
                  <!--<el-input v-model="dataForm.templateName" placeholder="模板组" clearable></el-input>-->
                  <el-select v-model="dataForm.groupId" placeholder="模板组">
                    <el-option
                      v-for="item in options"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                    <template slot="prefix">模板名称</template>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
      </el-card>
    </div>
    <el-table
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      :data="dataList"
      @sort-change="getSort"
      border
      stripe
      :height="tableHight"
      ref="codetemplateTable"
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      @row-click="toggleSelection"
      style="width: 100%;">
      <el-table-column
        type="selection"
        header-align="center"
        align="center"
        width="50">
      </el-table-column>
      <el-table-column
        prop="id"
        header-align="center"
        align="center"
        label="Id">
      </el-table-column>
      <el-table-column
        prop="templateName"
        header-align="center"
        align="center"
        sortable
        label="模板名称">
      </el-table-column>
      <el-table-column
        prop="templatePath"
        header-align="center"
        align="center"
        label="模板地址">
      </el-table-column>

      <el-table-column
        prop="fileName"
        header-align="center"
        align="center"
        label="文件名称">
      </el-table-column>
      <el-table-column
        prop="groupName"
        header-align="center"
        align="center"
        label="项目组名">
        <template slot-scope="scope">
          <el-tag size="small" v-if="scope.row.groupName "> {{ scope.row.groupName }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="localPath"
        header-align="center"
        align="center"
        label="本地路径">
      </el-table-column>
      <el-table-column
        prop="templateType"
        header-align="center"
        align="center"
        label="模板类型">
      </el-table-column>
      <el-table-column
        prop="userName"
        header-align="center"
        align="center"
        label="创建人">
      </el-table-column>
      <el-table-column
        prop="templateDesc"
        header-align="center"
        align="center"
        label="描述">
      </el-table-column>
  <!--    <el-table-column
        prop="originalId"
        header-align="center"
        align="center"
        label="原ID">
      </el-table-column>-->
<!--      <el-table-column
        prop="version"
        header-align="center"
        align="center"
        label="版本 1 正常版本 2 历史版本">
      </el-table-column>-->
      <el-table-column
        prop="language"
        header-align="center"
        align="center"
        label="语言">
      </el-table-column>
      <el-table-column
        fixed="right"
        header-align="center"
        align="center"
        width="200"
        label="操作">
        <template slot-scope="scope">
          <el-button type="text" size="small" v-on:click.stop="addOrUpdateHandle(scope.row.id)"> <icon-svg name="bianjisekuai" class="icon"></icon-svg>修改</el-button>
          <el-button type="text" size="small"  class="red" v-on:click.stop="deleteHandle(scope.row.id)"><icon-svg name="shanchu" class="icon"></icon-svg>删除</el-button>
          <el-button type="text" size="small"  v-on:click.stop="cloneHandle(scope.row.id)"><icon-svg name="guanlianshebei" class="icon"></icon-svg>克隆</el-button>
          <el-button type="text" size="small"  v-on:click.stop="uploademplate(scope.row.id)"><icon-svg name="get" class="icon"></icon-svg>更新模板</el-button>
          <el-button type="text" size="small"  v-on:click.stop="exportExcel(scope.row.id)"><icon-svg name="xiangxiazhanhang" class="icon"></icon-svg>下载模板</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChangeHandle"
      @current-change="currentChangeHandle"
      :current-page="pageIndex"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      :total="totalPage"
      layout="total, sizes, prev, pager, next, jumper">
    </el-pagination>
    <!-- 弹窗, 新增 / 修改 -->
    <add-or-update v-if="addOrUpdateVisible" ref="addOrUpdate" @refreshDataList="getDataList"></add-or-update>
      <!--弹窗上传-->
      <upload v-if="uploadVisible" ref="upload" @refreshDataList="getDataList"></upload>
  </div>
</template>
<script>
  import AddOrUpdate from './codetemplate-add-update'
  import Upload from './codetemplate-upload'
  export default {
    data () {
      return {
        dataForm: {
          templateName: '',
          groupId: ''
        },
        options: [],
        queryList: [{findType: 'LIKE', joinType: 'And', name: 'templateName'}],
        dataList: [],
        pageIndex: 1,
        pageSize: 20,
        totalPage: 0,
        sort: '',
        order: '',
        dataListLoading: false,
        dataListSelections: [],
        addOrUpdateVisible: false,
        uploadVisible: false,
        downloadDisable: false,
        offsetHeight: 0,
        tableHight: 0
      }
    },
    components: {
      AddOrUpdate,
      Upload
    },
    computed: {
      documentClientHeight: {
        get () { return this.$store.state.common.documentClientHeight }
      }
    },
    watch: {
      documentClientHeight: function (val) {
        this.tableHight = val - this.offsetHeight - 189
      }
    },
    activated () {
      this.getDataList()
      this.getGroup()
    },
    mounted () {
      this.offsetHeight = this.$refs.codetemplateFrom.offsetHeight
    },
    methods: {
      // 获取数据列表
      getQueryString () {
        let queryNew = []
        for (let key in this.dataForm) {
          if (this.dataForm[key]) {
            let found = false
            for (let i = 0; i < this.queryList.length; i++) {
              if (this.queryList[i].name === key) {
                this.queryList[i].values = []
                this.queryList[i].values.push(this.dataForm[key])
                queryNew.push(this.queryList[i])
                found = true
                break
              }
            }
            if (!found) {
              queryNew.push({values: [this.dataForm[key]], findType: 'EQ', joinType: 'And', name: key})
            }
          }
        }
        return JSON.stringify(queryNew)
      },
      getDataList () {
        // 添加查询的
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl('/codetemplate/list'),
          method: 'get',
          params: this.$http.adornParams({
            'page': this.pageIndex,
            'limit': this.pageSize,
            'order': this.order,
            'sort': this.sort,
            'json': this.getQueryString()
          })
        }).then(({data}) => {
          if (data && data.code === 0) {
            this.dataList = data.page.list
            this.totalPage = data.page.totalCount
          } else {
            this.dataList = []
            this.totalPage = 0
          }
          this.tableHight = this.$store.state.common.documentClientHeight - this.offsetHeight - 189
          this.dataListLoading = false
        })
      },
      getGroup () {
        this.$http.get(this.$http.adornUrl('/codetemplategroup/all'))
          .then(({data}) => {
            if (data && data.code === 0) {
              this.options = data.list
            }
          })
      },
      // 每页数
      sizeChangeHandle (val) {
        this.pageSize = val
        this.pageIndex = 1
        this.getDataList()
      },
      getSort (prop) {
        console.log(prop)
        this.sort = prop.prop
        this.order = prop.order
        this.getDataList()
      },
      // 当前页
      currentChangeHandle (val) {
        this.pageIndex = val
        this.getDataList()
      },
      // 多选
      selectionChangeHandle (val) {
        this.dataListSelections = val
      },
      // 触发一行的
      toggleSelection (row) {
        if (row) {
          this.$refs.codetemplateTable.toggleRowSelection(row)
        } else {
          this.$refs.codetemplateTable.clearSelection()
        }
      },
      // 新增 / 修改
      addOrUpdateHandle (id) {
        this.addOrUpdateVisible = true
        this.$nextTick(() => {
          this.$refs.addOrUpdate.init(id)
        })
      },
      uploademplate (id) {
        this.uploadVisible = true
        this.$nextTick(() => {
          this.$refs.upload.init(id)
          this.getDataList()
        })
      },
      // 克隆
      cloneHandle (id) {
        this.$http.post(this.$http.adornUrl('/codetemplate/clone'), this.$http.adornParams({id: id}))
          .then(({data}) => {
            if (data && data.code === 0) {
              this.getDataList()
            }
          })
      },
      exportExcel (id) {
        this.$axios({
          url: this.$http.adornUrl(`/codetemplate/export?token=${this.$cookie.get('token')}`),
          method: 'GET',
          responseType: 'blob',
          params: {
            'json': this.getQueryString()
          }
        }).then((response) => {
          const blob = new Blob([response.data], {type: response.data.type})
          const url = window.URL.createObjectURL(blob)
          const link = document.createElement('a')
          link.href = url
          let name = decodeURI(response.headers['name'])
          let fileName = 'unknown'
          if (name) {
            fileName = decodeURI(name)
          }
          link.setAttribute('download', fileName)
          document.body.appendChild(link)
          link.click()
          link.remove()
          window.URL.revokeObjectURL(url)
        })
      },
      handleResetSearch () {
        this.dataForm = Object.assign({})
      },
      // 删除
      deleteHandle (id) {
        let ids = id ? [id] : this.dataListSelections.map(item => {
          return item.id
        })
        this.$confirm(`确定对[id=${ids.join(',')}]进行[${id ? '删除' : '批量删除'}]操作?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl('/codetemplate/delete'),
            method: 'post',
            data: this.$http.adornData(ids, false)
          }).then(({data}) => {
            if (data && data.code === 0) {
              this.$message({
                message: '操作成功',
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.getDataList()
                }
              })
            } else {
              this.$message.error(data.msg)
            }
          })
        })
      }
    }
  }
</script>
<style scoped>
    .red{
        color: #ff0000;
    }
</style>



