<template>
  <el-dialog
    :title="!dataForm.id ? '新增' : '修改'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="100%"
    :fullscreen="true"
    center>
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="80px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="模板名称" prop="templateName">
            <el-input v-model="dataForm.templateName" placeholder="模板名称"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="路径" prop="templatePath">
            <el-input v-model="dataForm.templatePath"  placeholder="路径"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="生成代码路径" prop="localPath">
            <el-input v-model="dataForm.localPath" placeholder="生成代码路径"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="模板类型" prop="templateType">
            <el-select v-model="dataForm.templateType" placeholder="模板类型"
            >
              <el-option
                v-for="item in templateType"
                :key="item.label"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="文件名称" prop="fileName">
            <el-input v-model="dataForm.fileName" placeholder="文件名称"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="模板组" prop="groupId">
            <el-select v-model="dataForm.groupId" placeholder="组名"
                       @change="selectComplate">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>

        <el-col :span="12">
          <el-form-item label="语言" prop="language">
            <el-select v-model="dataForm.language" placeholder="语言"
            >
              <el-option
                v-for="item in language"
                :key="item.label"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="模板描述" prop="templateDesc">
        <el-input v-model="dataForm.templateDesc" placeholder="模板描述"></el-input>
      </el-form-item>
    </el-form>
    <div v-if="dataForm.language">
      <!--语法高亮-->
      <highlight-code :lang="dataForm.language" v-if="dataForm.content" >
        {{dataForm.content}}
      </highlight-code>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  import 'highlight.js/styles/default.css'
  export default {
    data () {
      return {
        visible: false,
        options: [],
        language: [{
          value: 'java',
          label: 'java'
        }, {
          value: 'xml',
          label: 'xml'
        }, {
          value: 'vue',
          label: 'vue'
        }, {
          value: 'javascript',
          label: 'javascript'
        }],
        templateType: [{
          value: 'beetl',
          label: 'beetl'
        }, {
          value: 'freemarker',
          label: 'freemarker'
        }],
        dataForm: {
          id: '',
          templateName: '',
          templatePath: '',
          templateDesc: '',
          fileName: '',
          groupId: '',
          localPath: '',
          templateType: '',
          crtUserId: '',
          content: ''
        },
        dataRule: {
          templateName: [
              { required: true, message: '不能为空', trigger: 'blur' }
          ],
          templatePath: [
              { required: true, message: '不能为空', trigger: 'blur' }],
          fileName: [
              { required: true, message: '不能为空', trigger: 'blur' }],
          groupId: [
              { required: true, message: '不能为空', trigger: 'blur' }],
          templateType: [
              { required: true, message: '不能为空', trigger: 'blur' }]
        }
      }
    },
    methods: {

      selectComplate (val) {
        let obj = {}
        obj = this.options.find(function (item) {
          return item.id === val
        })
        this.dataForm.templateName = obj.name
      },
      init (id) {
        this.dataForm.id = id || 0
        this.visible = true
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          this.$http.get(this.$http.adornUrl('/codetemplategroup/all'))
            .then(({data}) => {
              if (data && data.code === 0) {
                console.log(data)
                this.options = data.list
              }
            })
          if (this.dataForm.id) {
            this.$http({
              url: this.$http.adornUrl(`/codetemplate/info`),
              method: 'get',
              params: this.$http.adornParams({
                'id': this.dataForm.id
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.dataForm.templateName = data.codetemplate.templateName
                this.dataForm.templatePath = data.codetemplate.templatePath
                this.dataForm.templateDesc = data.codetemplate.templateDesc
                this.dataForm.fileName = data.codetemplate.fileName
                this.dataForm.groupId = data.codetemplate.groupId
                this.dataForm.localPath = data.codetemplate.localPath
                this.dataForm.templateType = data.codetemplate.templateType
                this.dataForm.crtUserId = data.codetemplate.crtUserId
                this.dataForm.originalId = data.codetemplate.originalId
                this.dataForm.version = data.codetemplate.version
                this.dataForm.content = data.codetemplate.content || ''
                this.dataForm.language = data.codetemplate.language
              }
            })
          }
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.$http({
              url: this.$http.adornUrl(`/codetemplate/${!this.dataForm.id ? 'add' : 'update'}`),
              method: 'post',
              data: this.$http.adornData({
                'id': this.dataForm.id || undefined,
                'templateName': this.dataForm.templateName,
                'templatePath': this.dataForm.templatePath,
                'templateDesc': this.dataForm.templateDesc,
                'fileName': this.dataForm.fileName,
                'groupId': this.dataForm.groupId,
                'localPath': this.dataForm.localPath,
                'templateType': this.dataForm.templateType,
                'crtUserId': this.dataForm.crtUserId,
                'originalId': this.dataForm.originalId,
                'language': this.dataForm.language
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              } else {
                this.$message.error(data.msg)
              }
            })
          }
        })
      }
    }
  }
</script>



